import '../styles/home.scss'

import {Header} from '../components/Header'
import pageImg from '../assets/images/pageicon.svg'
import backgroundImg from "../assets/images/background.svg"

export function Home() {

  return (
    <div style={{backgroundImage:`url(${backgroundImg})`, backgroundSize: "cover", backgroundRepeat: "no-repeat", minHeight: '100vh'}}>
      <Header></Header>
      <div className="home-container">
          <div className="left">
            <h2>Meu nome é <span>Victor Lomba</span>, sou um Web Developer que ama programar, foco em aprender novas tecnologias e produzir um código limpo e eficiente.</h2>
          </div>
          <div className="right">
              <img src={pageImg} alt="" />
          </div>
        
        
      </div>
    </div>
  )
}